:root {
  --primary-background: #ffffff;
  --primary-background-rgb: 255, 255, 255;
  --secondary-background: #559be7;
  --primary-text: #000000;
  --secondary-text: #ffffff;
  --special-text: #1776d5;
  --primary-border: #dddddd;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.theme-dark {
  --primary-background: #23272f;
  --primary-background-rgb: 35, 39, 47;
  --secondary-background: #70aae8;
  --primary-text: #ffffff;
  --secondary-text: #23272f;
  --special-text: #70aae8;
  --primary-border: #dddddd;
}
.theme-dark .header__toggle-theme::before {
  mask-image: url("../images/sprite.svg#light");
}
.theme-dark .skills__item-link--html {
  background-image: url("../images/icons/html-alt.svg");
}
.theme-dark .skills__item-link--css {
  background-image: url("../images/icons/css-alt.svg");
}
.theme-dark .skills__item-link--sass {
  background-image: url("../images/icons/sass-alt.svg");
}
.theme-dark .skills__item-link--react {
  background-image: url("../images/icons/react-alt.svg");
}
.theme-dark .skills__item-link--mssql {
  background-image: url("../images/icons/mssql-alt.svg");
}
.theme-dark .skills__item-link--mysql {
  background-image: url("../images/icons/mysql-alt.svg");
}
.theme-dark .skills__item-link--maria-db {
  background-image: url("../images/icons/maria-db-alt.svg");
}
.theme-dark .skills__item-link--figma {
  background-image: url("../images/icons/figma-alt.svg");
}
.theme-dark .works__item-content {
  background-color: rgba(0, 0, 0, 0.8);
}
.theme-dark .works__item-link:hover {
  box-shadow: 0 4px 10px rgba(255, 255, 255, 0.4);
}

.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  overflow: hidden;
}

@font-face {
  font-family: "PT Sans";
  src: url("../fonts/pt-sans-regular.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/pt-sans-bold.woff2") format("woff2");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.page {
  height: 100%;
}
.page--scroll-lock {
  overflow-y: hidden;
  scrollbar-gutter: stable;
}

.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  margin: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  color: var(--primary-text);
  background-color: var(--primary-background);
}

.header {
  padding: 30px 0 20px;
}

.header__inner {
  display: grid;
  grid-template-columns: 1fr auto auto;
  align-items: center;
  gap: 0 20px;
}
@media (min-width: 768px) {
  .header__inner {
    grid-template-columns: 1fr auto 1fr;
  }
}
.header__inner--simple {
  grid-template-columns: 1fr 1fr;
}

.header__logo::before {
  content: "";
  display: block;
  width: 40px;
  height: 40px;
  background-color: var(--secondary-background);
  mask-image: url("../images/sprite.svg#logo");
}
.header__logo[href]:hover {
  opacity: 0.8;
}
.header__logo[href]:active {
  opacity: 0.6;
}

.header__nav {
  grid-column: span 3;
  margin: 0 -30px;
}
@media (min-width: 768px) {
  .header__nav {
    grid-column: 2;
    grid-row: 1;
    margin: 0;
  }
}

.header__toggle-theme {
  justify-self: end;
  padding: 0;
  border: none;
  outline: none;
  background: transparent;
  cursor: pointer;
}
.header__toggle-theme:hover {
  opacity: 0.8;
}
.header__toggle-theme:active {
  opacity: 0.6;
}
.header__toggle-theme:focus-visible {
  outline: 4px solid var(--secondary-background);
}
.header__toggle-theme::before {
  content: "";
  display: block;
  width: 40px;
  height: 40px;
  background-color: var(--primary-text);
  mask-image: url("../images/sprite.svg#dark");
}

.header__toggle-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  width: 32px;
  height: 26px;
  border: none;
  outline: none;
  background-color: transparent;
  cursor: pointer;
}
@media (min-width: 768px) {
  .header__toggle-nav {
    display: none;
  }
}
.header__toggle-nav:hover {
  opacity: 0.6;
}
.header__toggle-nav:active {
  opacity: 0.3;
}
.header__toggle-nav:focus-visible {
  outline: 4px solid var(--secondary-background);
}

.header__toggle-nav--active > .header__toggle-nav-inner {
  background-color: transparent;
}
.header__toggle-nav--active > .header__toggle-nav-inner::before {
  width: 38px;
  transform: rotate(45deg) translate(0, -2px);
}
.header__toggle-nav--active > .header__toggle-nav-inner::after {
  width: 38px;
  transform: rotate(-45deg) translate(0, 2px);
}

.header__toggle-nav-inner {
  position: relative;
  display: inline-block;
  vertical-align: bottom;
  width: 100%;
  height: 2px;
  background-color: var(--primary-text);
  transform-origin: left center;
  transition: background-color 0.5s ease;
}
.header__toggle-nav-inner::before, .header__toggle-nav-inner::after {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: var(--primary-text);
  transform-origin: left center;
  transition: transform 0.5s ease;
}
.header__toggle-nav-inner::before {
  top: -12px;
}
.header__toggle-nav-inner::after {
  top: 12px;
}

.main {
  flex: 1 1 auto;
}
.main--simple {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media (min-width: 768px) {
  .main__intro {
    margin-top: 20px;
  }
}

.main__skills {
  margin-top: 40px;
}
@media (min-width: 768px) {
  .main__skills {
    margin-top: 50px;
  }
}

.main__works {
  margin-top: 40px;
}
@media (min-width: 768px) {
  .main__works {
    margin-top: 50px;
  }
}

.main__contacts {
  margin-top: 40px;
}
@media (min-width: 768px) {
  .main__contacts {
    margin-top: 50px;
  }
}

.footer {
  padding: 20px 0;
  background-color: var(--secondary-background);
  color: var(--secondary-text);
}

.footer__copyright {
  text-align: center;
}

.title-h1 {
  font-size: 28px;
  text-align: center;
}

.title-h2 {
  font-size: 24px;
}

.button {
  padding: 8px 20px;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 22px;
  font-weight: 700;
  color: var(--secondary-text);
  text-transform: uppercase;
  border: 0;
  background-color: var(--secondary-background);
  transition: 0.2s;
}
.button:hover {
  color: var(--secondary-background);
  outline: 2px solid var(--secondary-background);
  background-color: var(--primary-background);
  cursor: pointer;
}
.button:active {
  color: var(--secondary-text);
  outline: none;
  background-color: var(--secondary-background);
}

.container {
  margin: 0 auto;
  padding: 0 30px;
  width: 100%;
}
@media (min-width: 768px) {
  .container {
    padding: 0 40px;
  }
}
@media (min-width: 1280px) {
  .container {
    padding: 0;
    max-width: 1220px;
  }
}

.nav {
  position: relative;
  display: none;
}
@media (min-width: 768px) {
  .nav {
    display: flex;
    align-items: center;
  }
}
.nav--opened {
  display: flex;
}

.nav__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: stretch;
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  border-bottom: 1px solid var(--primary-border);
}
@media (min-width: 768px) {
  .nav__list {
    flex-direction: row;
    align-items: center;
    gap: 30px;
    border-bottom: none;
  }
}
@media (min-width: 1280px) {
  .nav__list {
    gap: 40px;
  }
}

.nav__item {
  border-top: 1px solid var(--primary-border);
}
@media (min-width: 768px) {
  .nav__item {
    border-top: none;
  }
}

.nav__link {
  display: block;
  padding: 20px 0;
  color: var(--primary-text);
  font-size: 20px;
  font-weight: 700;
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
}
.nav__link:hover > .nav__link-text {
  margin-bottom: -4px;
  padding-bottom: 4px;
  border-bottom: 4px solid var(--secondary-background);
  transition: 0.2s;
}
.nav__link:active {
  opacity: 0.6;
}
.nav__link:focus-visible {
  outline: 4px solid var(--secondary-background);
}
@media (min-width: 768px) {
  .nav__link {
    padding: 10px 5px;
    font-size: 22px;
  }
}

@media (max-width: 767px) {
  .nav--opened .nav__list {
    position: absolute;
    top: 20px;
    right: 0;
    left: 0;
    z-index: 10;
    background-color: var(--primary-background);
  }
}
.intro__inner {
  display: grid;
  justify-content: center;
  align-items: start;
  grid-template-columns: 1fr;
  gap: 30px;
}
@media (min-width: 768px) {
  .intro__inner {
    grid-template-columns: repeat(2, 1fr);
    grid-auto-rows: minmax(300px, 400px);
    gap: 40px;
  }
}
@media (min-width: 1280px) {
  .intro__inner {
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: 300px;
  }
}

.intro__content {
  grid-column: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
@media (min-width: 1280px) {
  .intro__content {
    grid-column: span 2;
  }
}

.intro__title {
  align-self: center;
  margin: 0 0 -4px;
  padding-bottom: 4px;
  border-bottom: 4px solid var(--secondary-background);
}

.intro__subtitle {
  align-self: center;
  font-weight: 700;
}

.intro__description {
  margin: 0;
}

.intro__link {
  color: var(--special-text);
}
.intro__link:hover {
  font-weight: 700;
}

.intro__picture {
  position: relative;
  width: 100%;
  height: 300px;
  border-radius: 20px;
  overflow: hidden;
}
@media (min-width: 768px) {
  .intro__picture {
    height: 100%;
  }
}

.intro__image {
  position: absolute;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.works__inner {
  text-align: center;
}

.works__title {
  display: inline-block;
  margin: 0 0 -4px;
  padding-bottom: 4px;
  text-transform: uppercase;
  border-bottom: 4px solid var(--secondary-background);
}

.works__list {
  display: flex;
  flex-direction: column;
  grid-template-columns: 1fr;
  grid-auto-rows: 300px;
  grid-auto-flow: dense;
  justify-content: center;
  gap: 30px;
  margin: 30px 0 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .works__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 40px;
  }
}
@media (min-width: 1280px) {
  .works__list {
    grid-template-columns: repeat(3, 1fr);
  }
}

.works__item {
  position: relative;
  flex: 1 0 300px;
}

.works__item.work--active {
  flex-basis: auto;
}

.works__area-button {
  display: flex;
  justify-content: center;
  align-self: center;
  margin-top: 40px;
}

.work {
  width: 100%;
  height: 100%;
  overflow: hidden;
  text-decoration: none;
  color: var(--primary-text);
  outline: 2px solid var(--primary-border);
  border-radius: 20px;
}

.work:hover:not(.work--active) {
  outline: 2px solid var(--secondary-background);
  cursor: pointer;
}

.work:focus-visible:not(.work--active) {
  outline: 4px solid var(--secondary-background);
}

.work__inner {
  display: flex;
  flex-direction: column;
  justify-content: end;
  height: 100%;
}

.work__content {
  align-self: end;
  display: flex;
  flex-direction: column;
  align-items: start;
  width: 100%;
  padding: 10px 20px 20px;
  background-color: rgba(var(--primary-background-rgb), 0.9);
}

.work__picture {
  position: absolute;
  z-index: -1;
  top: 0;
  width: 100%;
}

.work__image {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.work__title {
  margin: 0;
  text-align: left;
}

.work__description {
  margin: 10px 0 0;
  text-align: left;
}

.work__technologies {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin: 15px 0 0;
  padding: 0;
  list-style: none;
}

.work__technology {
  padding: 2px 6px;
  color: var(--secondary-text);
  border-radius: 6px;
  background-color: var(--secondary-background);
}

.work__about {
  display: none;
  text-align: left;
  margin: 20px 0 0;
  padding: 0 0 0 25px;
}

.work__about-item {
  margin: 0;
}

.work__footer {
  display: none;
}

.work__button {
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.work__button::before {
  content: "";
  display: block;
  width: 40px;
  height: 40px;
  background-color: var(--primary-text);
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
  transition: 0.2s;
}

.work__button:hover::before {
  background-color: var(--special-text);
}

.work__button:active::before {
  background-color: var(--primary-text);
  opacity: 0.6;
}

.work__button:focus-visible {
  outline: 4px solid var(--secondary-background);
}

.work__button--site::before {
  mask-image: url("../images/sprite.svg#web-site");
}

.work__button--source::before {
  mask-image: url("../images/sprite.svg#github");
}

.work__button--close::before {
  mask-image: url("../images/sprite.svg#close");
}

.work--active {
  grid-column: span 2;
  grid-row: span 2;
}
.work--active .work__content {
  margin-top: 200px;
}
@media (min-width: 768px) {
  .work--active .work__content {
    margin-top: 0;
  }
}
.work--active .work__about {
  display: block;
}
.work--active .work__footer {
  display: flex;
  justify-content: center;
  align-items: end;
  gap: 20px;
  padding: 0 20px 20px;
  background-color: rgba(var(--primary-background-rgb), 0.9);
}

.skills__inner {
  text-align: center;
}

.skills__title {
  display: inline-block;
  margin: 0 auto -4px;
  padding-bottom: 4px;
  text-transform: uppercase;
  border-bottom: 4px solid var(--secondary-background);
}

.skills__list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
  margin: 30px 0 0;
  padding: 0;
  list-style: none;
}

.skills__item-button {
  width: 100px;
  height: 100px;
  padding: 0;
  background-color: transparent;
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: center;
  border: none;
  cursor: pointer;
}
.skills__item-button:hover {
  opacity: 0.8;
}
.skills__item-button:active {
  opacity: 0.6;
}
.skills__item-button:focus-visible {
  outline: 4px solid var(--secondary-background);
}

.contacts {
  padding: 0 0 40px;
}

.contacts__inner {
  text-align: center;
}

.contacts__title {
  display: inline-block;
  margin: 0 0 -4px;
  padding-bottom: 4px;
  text-transform: uppercase;
  border-bottom: 4px solid var(--secondary-background);
}

.contacts__list {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
  margin: 20px 0 0;
  padding: 0;
  list-style: none;
}

.contacts__item-link {
  display: flex;
}
.contacts__item-link::before {
  content: "";
  width: 50px;
  height: 50px;
  background-color: var(--primary-text);
  transition: 0.2s;
}
.contacts__item-link:hover::before {
  background-color: var(--special-text);
}
.contacts__item-link:active::before {
  background-color: var(--primary-text);
  opacity: 0.6;
}
.contacts__item-link:focus-visible {
  outline: 4px solid var(--secondary-background);
}
.contacts__item-link--mail::before {
  mask-image: url("../images/sprite.svg#mail");
}
.contacts__item-link--telegram::before {
  mask-image: url("../images/sprite.svg#telegram");
}
.contacts__item-link--github::before {
  mask-image: url("../images/sprite.svg#github");
}

.modal[open] {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  width: 100%;
  height: 100%;
  border: none;
  background-color: transparent;
}
.modal[open]::backdrop {
  background-color: rgba(0, 0, 0, 0.5);
}

.modal__inner {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  width: 100%;
  max-width: 500px;
  color: var(--primary-text);
  background-color: var(--primary-background);
  border-radius: 20px;
}

.modal__title {
  margin: 20px 0 0;
  text-align: center;
}

.modal__text {
  text-align: center;
}

.modal__about {
  margin: 20px 0 0;
  padding: 0 0 0 25px;
}

.modal__close {
  position: absolute;
  top: 20px;
  right: 20px;
  padding: 0;
  background-color: transparent;
  border: 0;
  outline: 0;
  cursor: pointer;
}
.modal__close:hover::before {
  background-color: var(--special-text);
  transition: 0.2s;
}
.modal__close:active::before {
  background-color: var(--primary-text);
  opacity: 0.6;
}
.modal__close:focus-visible {
  outline: 4px solid var(--secondary-background);
}
.modal__close::before {
  content: "";
  display: block;
  width: 30px;
  height: 30px;
  mask-image: url("../images/sprite.svg#close");
  background-color: var(--primary-text);
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
  transition: 0.2s;
}

.not-found {
  display: flex;
  flex-direction: column;
  place-content: center;
  align-items: center;
  gap: 30px;
  height: 100%;
  padding: 0 0 150px;
}

.not-found__picture {
  width: 100%;
  max-width: 300px;
  height: 150px;
}
.not-found__picture::before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  mask-image: url("../images/sprite.svg#404");
  background-color: var(--secondary-background);
  background-size: 100%;
  background-position: center;
  background-repeat: no-repeat;
}

.not-found__title {
  margin: 0;
  text-align: center;
}